.title{
    text-align: center;
}
.DisplayWorkContainer{
    display: flex;
    width:75%;
    margin: 10px auto;
    justify-content: space-evenly;
    flex-wrap: wrap;
    .Project{
        margin: 20px 0;
        position: relative;
        overflow: hidden;
        width: 250px;
        height: 250px;
        .ProjectInfo{
            position: absolute;
            top:0;
            left: 0;
            display:flex;
            justify-content: center;
            align-items: center;
            align-content: center;
            text-align:center;
            width: 100%;
            height: 100%;
            flex-wrap: wrap;
            transform: scale(3);
            opacity:0;
            transition: all 0.3s;
            background: rgba(0,0,0,.7);
            color:white;
            .ProjectTitle{
                margin:0px;
                width: 100%;
            }
            .ProjectPrice{
                margin: 0;
                width: 100%;
            }
            .ProjectDetailsButton{
                background: transparent;
                border:2px solid white;
                color:white;
                margin:10px 0;
                padding: 10px;
                font-size: 16px;
                transition: all 0.3s;
            }
            .ProjectDetailsButton:hover{
                color:black;
                background:rgba(255,255,255,.7);
                cursor: pointer;
            }
        }
    }
    .Project:hover{
        .ProjectInfo{
            opacity: 1;
            transform: scale(1);
        }
    }
}
.Pager{
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
    .CurrentPage{
        color:white;
        background: black;
        font-weight: bold;
    }
    div{
        border-radius: 15px;
        width: 30px;
        height: 30px;
        font-weight: bold;
        font-size: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 5px; 
        border:1px solid rgba(0,0,0,.5);
        transition: all .3s;
    }
    div:hover{
        cursor: pointer;
        background: rgba(0,0,0,.5)
    }

}